.card {
  padding: 20px;
  margin-bottom: 20px;
  min-width: 270px;
  background: #1d1d1d;
  color: #7c7c7c; }
  .card .direction {
    display: flex;
    margin-bottom: 20px; }
    .card .direction .from, .card .direction .to {
      flex-grow: 1;
      font-size: 14px; }
    .card .direction .city {
      color: #fff;
      font-size: 24px;
      margin-top: 5px; }
  .card .time-block {
    margin-bottom: 20px; }
    .card .time-block .departure {
      margin-bottom: 10px; }
    .card .time-block .title {
      margin-bottom: 5px; }
    .card .time-block .time {
      color: #fff;
      font-size: 18px; }
  .card .carrier-name {
    font-size: 24px; }
